.commentBox {
    margin: 64px 0 24px 0;

    .commentText {
        margin-top: 20px;
        font-size: 14px;
        color: #444;
        font-weight: 400;
        text-align: justify;
        max-width: 380px;
    }

    .commentFooterValue {
        border: 1px solid #ddd;
        padding: 8px;
        margin: 0 0px 0 16px;
        border-radius: 16px;
        cursor: pointer;
        background-color: #f2f2f2;
    }
}


.noteBox {
    flex: 2;

    .barValue {
        position: relative;
        height: 8px;
        // width: 60%;
        margin: 8px 0 20px 0;
        background-color: #dbdbdb;
        border-radius: 4px;
        box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 10%);

    }

    .before {
        content: " ";
        position: absolute;
        top: -8px;
        left: 0;
        height: 8px;
        width: 0%;
        border-radius: 4px;
        background-color: #eab413;
    }

    &:hover .barValue {
        box-shadow: 0px 4px 8px 0px rgb(0 0 0 / 10%);
        transition: 0.3s all ease-in-out;
    }

    .barTitle{
        font-size: 1em;
    }
}

.commentFooter {
    margin-top: 24px;
}

.green {
    color: #00a3b4;
}

.red{
    color: #E64A19;
}

.redBackground{
    background-color: #E64A19 !important;
}