.content {

    display: grid;
    grid-template-columns: 4fr 8fr;
    grid-gap: 20px;
    max-width: 1300px;
    margin: 0 auto;
    justify-content: center;

    grid-template-areas:
        "header header"
        "profil dash"
        "left right";

    @media screen and (max-width: 820px) {
        grid-template-areas:
            "header header"
            "profil profil"
            "dash dash"
            "left left"
            "right right";
    }

}

// .content.reverse {
//     grid-template-columns: 6fr 4fr;
// }

.header {
    //background-color: "#009688";

    grid-area: header;
    max-width: 1300px;
    height: 320px;

    margin: 20px;

    img {
        object-fit: cover;
        width: 100%;
        filter: blur(0px);

        // box-shadow: 0px 4px 8px 0px rgb(0 0 0 / 10%);
    }

    @media screen and (max-width: 820px) {

        height: 240px;
        margin: 0px;

        img {
            border-radius: 0;
        }
    }
}

.profil {
    display: flex;
    grid-area: profil;
    min-width: 200px;
    flex-direction: row;
    gap: 36px;
    width: calc(100% - 4em);
}

.dash {
    //background-color: "#d4a380";
    width: 100%;
    height: 84px;
    padding: 0 20px;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 6px;

    .dashItem {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 6px;
        color: #757575;

        .score {
            font-weight: bold;
            color: #000;
        }
    }
}

.dashContent {

    grid-area: dash;
    padding: 20px 0;
    margin: 0 20px;
    //  box-shadow: 0px 4px 8px 0px rgb(0 0 0 / 10%);
    border-radius: 16px;
}

.left {
    // background-color: #f44336;
    grid-area: left;
    // min-width: 320px;
    height: auto;
    padding: 0 20px;
    // height: 400px;
    overflow: scroll;
    height: 80vh;
    border-right: solid 1px #ddd;


}

.right {
    // background-color: #eab413;
    grid-area: right;
    width: 100%;
    padding: 12px 20px;
    //max-height: 800px;
    position: sticky;
    top: 86px;
    // height: 400px;
    overflow: scroll;
    height: 100vh;
}


.distanceBox {
    display: grid;
    gap: 20px;
    grid-template-columns: 3fr 3fr 3fr 3fr;
    justify-content: center;
    align-items: center;
    align-content: center;

    .distanceItem {
        display: flex;
        gap: 20px;
        justify-content: flex-start;
        align-items: center;

        .distanceIcon {
            font-size: 18px;
        }
    }
}

.tabulation {
    margin-left: 64px;
}